

import React from 'react';
import './App.css';
import SearchBar from '../SearchBar/SearchBar';
import SearchResults from '../SearchResults/SearchResults';
import Playlist from '../Playlist/Playlist';
import Spotify from '../../util/Spotify';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchResults: [],
      playlistName: 'My Playlist',
      playlistTracks: [],
      term: '',
    };
    this.addTrack = this.addTrack.bind(this);
    this.removeTrack = this.removeTrack.bind(this);
    this.updatePlaylistName = this.updatePlayListName.bind(this);
    this.savePlaylist = this.savePlaylist.bind(this);
    this.search = this.search.bind(this);
    this.handleTermChange = this.handleTermChange.bind(this);
  };

  handleTermChange(event) {
    this.setState({
      ...this.state,
      term: event.target.value
    })
}

  search() {
    console.log(this.state.term)
    Spotify.search(this.state.term)
    .then(searchResults => {
      console.log(searchResults)
      this.setState({
        searchResults: searchResults
      })
    })
  }

  savePlaylist() {
   const trackUris = this.state.playlistTracks.map(track => track.uri);
   Spotify.savePlaylist(this.state.playlistName, trackUris).then(() => {
     this.setState({
       playlistName: 'New Playlist',
       playlistTracks: []
     })
   })
  }

  addTrack(track) {
    let tracks = this.state.playlistTracks;
    if (tracks.find(savedTrack => savedTrack.id === track.id)) {
      return;
    }
    tracks.push(track);
    this.setState({playlistTracks: tracks});
  }
  removeTrack(track) {
    console.log(track)
    let tracks = this.state.playlistTracks;
    tracks = tracks.filter(currentTrack => currentTrack.id !== track);
    this.setState({playlistTracks: tracks});
  }
  updatePlayListName(name) {
    this.setState({playlistName: name});
  }
  render() {
    return(
      <div>
  <h1>Ja<span className="highlight">mmm</span>ing</h1>
  <div className="App">
    <SearchBar onSearch={this.search} handleTermChange={this.handleTermChange}/>
    <div className="App-playlist">
      <SearchResults searchResults={this.state.searchResults} 
      onAdd={this.addTrack}
      />
      <Playlist tracks={this.state.searchResults} playlistName={this.state.playlistName}
      playlistTracks={this.state.playlistTracks} onRemove={this.removeTrack}
      onNameChange={this.updatePlaylistName} onSave={this.savePlaylist} 
      />
    </div>
  </div>
</div>
    )
  }
}

export default App;
